import { Component, OnInit } from '@angular/core';
import { SelectItem } from 'primeng/api';

@Component({
  selector: 'app-dream-work',
  templateUrl: './dream-work.component.html',
  styleUrls: ['./dream-work.component.scss']
})
export class DreamWorkComponent implements OnInit {
  diadiem:SelectItem[];
  nganhNge:SelectItem[];
  phucLoi:SelectItem[];
  x:string;
  y:string;
  constructor() { 
    this.diadiem = [
      {label: 'Si gn', value: 'Si gn'},
      {label: 'H Ni', value: 'H Ni'},
    ];
    this.nganhNge = [
      {label: 'Phn mm', value: 'Phn mm'},
      {label: 'Phn cng', value: 'Phn cng'},
      {label: 'Qun vin', value: 'Qun vin'},
      {label: 'Mng/IT', value: 'Mng/IT'}
    ];
    this.phucLoi = [
      {label: 'Giai thng', value: 'Giai thng'},
      {label: 'Tin thng', value: 'Tin thng'},
      {label: 'Cn tin', value: 'Cn tin'},
      {label: 'Chm sc sc khe', value: 'Chm sc sc khe'},
      {label: 'Th vin', value: 'Th vin'},
      {label: 'o to', value: 'o to'},
      {label: 'Xe a n', value: 'Xe a n'},
    ];
  }

  ngOnInit() {
    this.x="visible";
    this.y="hidden";
  }
  onClick()
  {
    this.x="hidden";
    this.y="visible";
  }
  Edit()
  {
    this.x="visible";
    this.y="hidden";
  }
}
