import { Component, OnInit } from '@angular/core';
import { SelectItem } from 'primeng/api';

@Component({
  selector: 'app-education-candidate',
  templateUrl: './education-candidate.component.html',
  styleUrls: ['./education-candidate.component.scss']
})
export class EducationCandidateComponent implements OnInit {
  bangCap: SelectItem[];
  x:string;
  y:string;
  constructor() { 
    this.bangCap = [
      {label:'Vui lng chn bng cp', value:null},
      {label:'i hc', value:{id:1, name: 'i hc', code: 'NY'}},
      {label:'Cao ng', value:{id:2, name: 'Cao ng', code: 'RM'}},
      {label:'Trung cp', value:{id:3, name: 'Trung cp', code: 'LDN'}},
  ];
  }

  ngOnInit() {
    this.x="visible";
    this.y="hidden";

  }
  onClick()
  {
    this.x="hidden";
    this.y="visible";
  }
  Edit()
  {
    this.x="visible";
    this.y="hidden";
  }

}
